.item{
    margin: 25px 0;
    padding: 10px;
    border-radius: 10px;
    border: 2px solid #ccc;
    background-color: rgba(100, 110, 255, 0.3);
    overflow: hidden;
    position: relative;
}

.postInfo {
    width: 60px;
    float: left;
    overflow: hidden;
}

.item img{
    float: left;
    width: 50px;
    height: 50px;
    border-radius: 25px;
    border: 1px solid #a1a1a1;
    overflow: hidden;
    margin-bottom: 25px;
}

.message {
    overflow: hidden;
    padding: 0 10px 0 10px;
}

.likes {
    margin-top: 20px;
    position: absolute;
    bottom: 3px;
}

.like{
    margin-left: 5px;
    cursor: pointer;
    display: inline-block;
    font-size: 16px;
    color: #546b9c;
}

.like:hover{
    color: #ff8234;
}

.likesCounts {
    float: right;
    margin-left: 5px;
    font-size: 15px;
    padding-bottom: 5px;
}
