.profile_info {
    margin-bottom: 30px;
    overflow: hidden;
}

.img img{
    max-width: 100%;
    margin-left: 5px;
}

.myProfile{
    margin-top: 20px;
}

.myProfile .mainPhoto {
    float: left;
    margin-left: 5%;
    margin-right: 5%;
    position: relative;
}


.mainPhoto img {
    max-width: 150px;
    margin-top: 10px;
}

.mainPhoto .fileUpload input {
    bottom:-40px;
    left: -20px;
    display: none;
}

.mainPhoto .fileUpload {
    overflow: hidden;
    position: relative;
    width: 80px;
    height: 20px;
    background-color: #7d7ed7;
    color: #fff;
    margin-left: 35px;
    margin-top: 10px;
    font-size: 12px;
    cursor: pointer;
    text-align: center;
    transition: 0.25s;
}

.mainPhoto .fileUpload:hover{
    background-color: #6f51b2;
}

.fileUpload label {
    display: block;
    position: absolute;
    top: 1px;
    left: 0;
    width: 100%;
    height: 100%;
    cursor: pointer;
}

.profileStatus{
    color: #545466;;
    margin-bottom: 10px;
}

.info{
    max-width: 60%;
    overflow: hidden;
}

.info button {
    width: 80px;
    float: right;
    color: white;
    background-color: #7d7ed7;
    border: none;
    padding: 4px 20px;
    cursor: pointer;
    vertical-align: top;
    transition: 0.25s;
    font-weight: 700;
    margin-top: 5px;
}

.info button:hover{
    background-color: #6f51b2;
}


.name h2{
    margin-top: 0;
    margin-bottom: 10px;
}

.profileStatus input{
    min-width: 180px;
    box-sizing: border-box;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-family: Roboto,sans-serif;
    color: #37374b;
    width: 30%;
    padding: 2px;
}

.item{
    max-width: 80%;
    margin-bottom: 8px;
}

.item .contacts strong{
    display: block;
    margin-bottom: 5px;
}

.contactsItems{
    margin-left: 20px;
    overflow: hidden;
}

.contactsItem{
    margin-bottom: 5px;
}




