.dataForm{
    max-width: 60%;
    overflow: hidden;
}

.dataForm button {
    width: 80px;
    float: right;
    color: white;
    background-color: #7d7ed7;
    border: none;
    padding: 4px 20px;
    cursor: pointer;
    vertical-align: top;
    transition: 0.25s;
    font-weight: 700;
    margin-top: 5px;
}

.dataForm button:hover{
    background-color: #6f51b2;
}

.dataForm .name{

}

.dataForm input, textarea{
    margin: 3px 0 5px 3px;
    min-width: 180px;
    box-sizing: border-box;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-family: Roboto,sans-serif;
    color: #37374b;
    width: 60%;
    padding: 2px;
}

.dataForm input[type=checkbox]{
    min-width: 20px;
    width: 5px;
}

.dataForm .item{
    margin-bottom: 8px;
}

.dataForm .item .contacts strong{
    display: block;
    margin-bottom: 5px;
}

.dataForm .contactsItems{
    margin-left: 20px;
    overflow: hidden;
}

.dataForm .contactsItem{
    margin-bottom: 5px;
}




