.myPosts {
    max-width: 650px;
    margin-top: 20px;
    margin-left: 7%;
}

.myPosts h3{
    margin-left: 10px;
    width: 100%;
}

.newPost{
    margin-bottom: 60px;
}

.newPost textarea {
    width: 100%;
    height: 100px;
    padding: 12px 20px;
    box-sizing: border-box;
    border: 2px solid #ccc;
    border-radius: 10px;
    background-color: #eefaff;
    font-family: Roboto,sans-serif;
    margin-bottom: 10px;
}

.newPost button {
    float: right;
    background-color: #4CAF50;
    border: none;
    color: white;
    padding: 10px 30px;
    text-decoration: none;
    margin: 0 5px;
    cursor: pointer;
    font-weight: 700;
    transition: 0.25s;
}

.newPost button:hover{
    background-color: #3ddf44;
    color: white;
}

.newPost button:active{
    background-color: #4CAF50;
}

